@charset "UTF-8";
@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/Fira_Sans/FiraSans-Regular.ttf") format("ttf");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Fira Sans";
  src: url("fonts/Fira_Sans/FiraSans-Medium.ttf") format("ttf");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Bold.ttf") format("ttf");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 24px;
  color: #000000;
  box-sizing: border-box;
  position: relative; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.container {
  width: 1024px;
  margin: 0 auto;
  box-sizing: border-box; }

@media (max-width: 768px) {
  .container {
    width: 768px;
    padding: 0 50px; } }

@media (max-width: 320px) {
  .container {
    width: 320px; } }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  padding-top: 32px;
  padding-bottom: 826px;
  position: relative;
  background-image: url("../../img/bg.png");
  background-repeat: no-repeat;
  background-position: center 0;
  z-index: 22; }
  .page-header::after {
    position: absolute;
    left: 515px;
    top: 169px;
    content: "";
    width: 381px;
    height: 609px;
    background: url("../../img/photo_girl.png") no-repeat;
    z-index: 1; }

.page-header__logo {
  position: absolute;
  top: 32px;
  left: 50%; }

.page-header__nav {
  width: 1024px;
  margin: 0 auto;
  position: relative; }
  .page-header__nav::after {
    content: "";
    width: 1018px;
    height: 340px;
    position: absolute;
    background: url("../../img/Израиль.png") no-repeat; }

.page-header__list {
  padding: 0;
  display: flex;
  justify-content: space-between;
  list-style: none;
  color: #484848; }

.page-header__item--tel {
  color: #484848; }

.page-header__item--call {
  border-bottom: 1px dashed #484848; }
